import type { LoaderFunction } from '@remix-run/node';

import { useLoaderData } from '@remix-run/react';
import Books from '~/components/books';
import { json } from '@remix-run/node';
import { getUserIdFromRequest } from '~/utils/cookies.server';
import { db } from '~/utils/db.server';

type BooksData = {
	books: Array<{
		id: string;
		title: string;
		authors: Array<{ author: { id: string; name: string } }>;
		users: Array<{
			readAt: Date | null;
			readingOrder: number | null;
		}>;
		series: {
			name: string;
		} | null;
	}>;
};

export const loader: LoaderFunction = async ({ request }) => {
	const userId = await getUserIdFromRequest(request);

	if (!userId) {
		const emptyData: BooksData = {
			books: [],
		};
		return json(emptyData);
	}

	const data: BooksData = {
		books: await db.book.findMany({
			select: {
				id: true,
				title: true,
				users: {
					where: { userId },
					select: {
						readAt: true,
						readingOrder: true,
					},
					take: 1,
				},
				authors: {
					select: {
						author: { select: { id: true, name: true } },
					},
				},
				series: {
					select: {
						name: true,
					},
				},
			},
		}),
	};

	return json(data);
};

export default function Index() {
	const loaderData = useLoaderData<BooksData>();

	return <Books loaderData={loaderData} />;
}
